.container {
  position: relative;
  width: 100%;
  padding: 100px 0;
}
.wrapper {
  position: relative;
  max-width: var(--window-max-width);
  width: var(--width-width);
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  grid-gap: 50px;
}
.left-part {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
}
.left-part h2 {
  white-space: nowrap;
  position: relative;
  font-size: 350%;
}
.left-part p {
  white-space: nowrap;
  position: relative;
  font-style: italic;
}
.right-part-wrapper {
  position: relative;
  width: 100%;
  border: 2px solid transparent;
  border-image: linear-gradient(90deg,#dedede,#fff);
  border-image-slice: 1;
  padding: 40px 30px;
  padding-right: 0;
}
.right-part {
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 100px;
}
.right-item-block {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 35px;
}
.right-item-block img{
  position: relative;
  border-radius: 50%;
  width: 110px;
  height: 110px;
  background: #F9E0C2;
}
.right-item-block h4 {
  font-size: 140%;
}
.right-item-block p {

}
.right-item-block button{
  position: relative;
  text-decoration: underline;
  color: #E89732;
  align-self: start;
  margin-top: auto;
  margin-bottom: 0;
}

.svg-wrapper {
  position: absolute;
  z-index: 0;
  top: -280px;
  left: 0px;
}